html{
    box-sizing: border-box;
    font-family: sans-serif;
}

*,
*::before,
*::after{
    box-sizing: inherit;
}

body{
    margin: 0;
    overflow-x: hidden;
    background: url("./background-1.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

 
img{
    max-width: 100%;
    height: auto;
}

h1,
h2,
h3,
h4,
p{
    margin: 0 !important;
    line-height: 1 !important;
}

.container{
    width: 90%;
    text-align: center;
    margin: 0 auto;
}

.glass{
    box-shadow: 0px 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur( 3px );
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.18);
}

.glass-white{
    background: rgba(255, 255, 255, 0.50);
}

.glass-blue{
    background: rgba(8, 90, 197, 0.5);
}

/***********************************************************
************************************************************/
.header{
    margin-top: 15px ;
}


.selection h2{
    font-size: 2rem;
    color: #000;
    margin: 0;
}

.op-button{
    font-family: Arial, Helvetica, sans-serif;
    background-color: #1890FF;
    border: none;
    width: 50px;
    height: 50px;
    font-size: 2rem;
    font-weight: bold;
    margin: 10px;
    border-radius: 12px;
    box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, .3);
    cursor: pointer;
}



.score h4,
.score p{
    padding: 0;
    display: block;
}

.score h4{
    font-size: 1rem;
}

.score p{
    font-size: 2.8rem;
    font-weight: bold;
}

.lives{
    font-size: 1rem;
    margin-bottom: 5px;
}

.hearts{
    color: red !important;
    font-size: 2rem !important;
    margin: 0 8px;
}

.losed-heart{
    color: gray !important;
    font-size: 2rem !important;
    margin: 0 8px;
}

.operation-buttons{
    width: 100%;
    height: 120px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 50px;
}

.operation-buttons p{
    background-color: blueviolet;
    width: 80px;
    height: 80px;
    font-size: 4rem;
    border-radius: 12px;
    box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.5);
}


.result-container{
    width: 100%;
    margin-top: 15px;
}



.result-buttons{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.result-buttons button{
    border: none;
    background-color: rgb(2, 182, 32);
    width: 100px;
    height: 100px;
    font-size: 3rem;
    font-weight: bold;
    border-radius: 50px;
    box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.5);
    align-self: center;
    margin: 10px 0;
    cursor: pointer;
}



/* 
*********************************************************************
                Media queries
********************************************************************* 
*/


@media (min-width: 768px) {
    .selection h2{
        font-size: 2.5rem;
        color: #000;
        margin: 0;
    }
    
    .op-button{
        font-size: 2rem;
    }
    
    .score h4{
        font-size: 2rem;
    }

    .score p{
        font-size: 3rem;
        font-weight: bold;
    }

    .operation-buttons{
        height: 160px;
    }
    
    .operation-buttons p{
        width: 130px;
        height: 130px;
        font-size: 6.2rem;
        font-weight: bold;
        border-radius: 22px;
    }

    .result-container{
        margin-top: 30px;
    }
    
    
    
    .result-buttons{
        justify-content: space-evenly;
    }
    
    .result-buttons button{
        width: 120px;
        height: 120px;
        font-size: 4rem;
        border-radius: 60px;
    }
}


@media (min-width: 992px) {
    .score h4{
        margin-top: 10px !important;
    }

    .lives{
        font-size: 2rem;
        font-weight: bold;
    }
    
    .hearts{
        font-size: 3rem !important;
        margin: 0 20px;
    }
    
    .losed-heart{
        font-size: 3rem !important;
        margin: 0 20px;
    }
  
    .result-buttons{
        flex-direction: row;
    }
}


@media (min-width: 1200px) {

    .op-button{
        width: 70px;
        height: 70px;
        font-size: 2.8rem;
    }
}
